.App {
  text-align: center;
  background-color: #fff;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  line-height: 60px;
  font-size: 30px;
  color: black;
  border-bottom: 1px solid #eee;
}

.App-link {
  color: #61dafb;
}

.wordle-container {
  width: 320px;
  max-width: 100%;
  margin: 40px auto;

  display: flex;
  flex-direction: column;

  .wordle-row {
    display: flex;
    flex-direction: row;
    user-select: none;

    .wordle-letter {
      position: relative;
      width: 60px;
      height: 60px;
      flex-grow: 1;
      flex-shrink: 1;

      box-sizing: border-box;
      border: 1px solid #ccc;

      line-height: 60px;
      font-size: 40px;
      font-weight: bold;

      &.exact { background-color: #2bb673; }
      &.exists { background-color: #f2b336; }
      &.wrong { background-color: #888; }

      user-select: none;

      .hint {
        position: absolute;
        right: 0px;
        bottom: 0px;
        font-size: 14px;
        //font-weight: normal;
        color: #888;
        padding: 3px;
        line-height: 1em;

        user-select: none;
      }
    }

    .wordle-letter + .wordle-letter { margin-left: 5px; }
  }

  .wordle-row + .wordle-row { margin-top: 5px; }


  .wordle-help {
    text-align: left;
    font-size: 15px;
    padding-top: 30px;

    li {
      margin-top: 5px;
    }
  }
}